//vuelidate
import Vuelidate from 'vuelidate'
Vue.use(Vuelidate)

import axios from 'axios'
import VueAxios from 'vue-axios'

//var instance = axios.create();
// Add a response interceptor
axios.interceptors.response.use(function (response) {
  // Do something with response data
  return response;
}, function (error) {
  // Do something with response error
  if ( (error.response.status+"").startsWith("50")) {
    window.location = '/error';
  }
  if (400 == error.response.status) {
    window.location = '/';
  } else if ([401, 403].includes(error.response.status)) {
    localStorage.clear();
    window.location = '/';
  } else {
    return Promise.reject(error);
  }
});
//Vue.use(VueAxios, instance)// axios)

Vue.use(VueAxios,  axios)

//progress bar
import CripLoading from "crip-vue-loading"


// Install component in to Vue instance and inject in to axios.
//Vue.use(CripLoading, {instance})
Vue.use(CripLoading, {axios})

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
